import { state } from "../state";
export function initResultComp() {
  class ResultComp extends HTMLElement {
    shadow: ShadowRoot;
    constructor() {
      super();

      this.shadow = this.attachShadow({ mode: "open" });
      this.render();
    }
    render() {
      const currentState = state.getState();

      const style = document.createElement("style");
      style.innerHTML = `
      @import url('https://fonts.googleapis.com/css2?family=Odibee+Sans&display=swap');
      @import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');
      .container{
        background-color:red;
        width:300px;
        height:120px;
          border: solid 4px black;
        border-radius:16px;
        display:flex;
        margin:0 auto;
        text-align:center;
        justify-content:center;
        align-items:center;
        font-size:25px;
          letter-spacing: 5px;

          font-weight: 400;
          font-family: "Odibee Sans";
          box-shadow: 0 20px 40px gray; /* Sombra inicial dorada */
      }
      .container:hover{
        width:330px;
        height:130px;
         transition: width 1s ease, height 1s ease,box-shadow 1s ease;
      }
      .container-score{
            font-size:20px;
          font-weight: 600;
          font-family: 'Bebas Neue', sans-serif;
            background-color: rgba(255, 255, 255, 0.3);
         box-shadow: 0 20px 40px black; /* Sombra inicial dorada */
          letter-spacing: 2px;

        width:300px;
        height:200px;
           text-align:center;
        justify-content:center;
        align-items:center;
        border: solid 4px black;
        border-radius:6px;
         display:flex;
        margin:0 auto;
        flex-direction:column;
      }
         .container-score:hover{
        width:340px;
        height:210px;
         transition: width 1s ease, height 1s ease,box-shadow 1s ease;
      }
     .container-score::before {
  content: '';
  position: absolute;
  top: -4px; /* Ajusta la posición para que la línea esté justo fuera del borde superior */
  left: -4px; /* Ajusta la posición para que la línea esté justo fuera del borde izquierdo */
  height: 4px; /* Ancho de la línea */
  width: 0;
  background-color: #00f;
  animation: moveLine 2s linear infinite; /* Ajusta la duración y el tipo de animación según tus preferencias */
}

@keyframes moveLine {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}
    .fireworks-container {
          position: relative;
          width: 100%;
          height: 100%;
        }
        

        .fireworks {
          position: absolute;
          width: 2px;
          height: 10px;
          background-color: #ffeb3b;
          animation: explode 1s infinite;
        }

        @keyframes explode {
          0% {
            transform: translateY(0) rotate(0deg);
            opacity: 0;
          }
          25% {
            opacity: 1;
          }
          50% {
            transform: translateY(-50px) rotate(180deg);
            opacity: 0;
          }
          75% {
            opacity: 1;
          }
          100% {
            transform: translateY(0) rotate(360deg);
            opacity: 0;
          }
        }
        `;
      this.shadow.appendChild(style);
      const shadow = document.createElement("shadow");
      const div = document.createElement("div");
      const history = state.getHistory();

      div.innerHTML = `
      <div class="container">
      <h2>${currentState.currentGame.ganador}</h2>
      </div><br>
      <div class="container-score">
      <p>Mis puntos: ${history[0].myPoint}</p>
      <p>PC puntos: ${history[0].computerPoint}</p>
      </div>
        `;
      const cuadradoGanador = div.querySelector(".container");

      if (currentState.currentGame.ganador == "Ganaste") {
        (cuadradoGanador as any).style.backgroundColor = "#6CB46C";
        createFireworks();
      } else if (currentState.currentGame.ganador == "Empate") {
        (cuadradoGanador as any).style.backgroundColor = "#006CFC";
        createFireworks();
      }
      function createFireworks() {
        const fireworksContainer = document.createElement("div");
        fireworksContainer.classList.add("fireworks-container");
        div.appendChild(fireworksContainer);

        for (let i = 0; i < 5; i++) {
          const firework = document.createElement("div");
          firework.classList.add("firework"); // Cambié "fireworks" a "firework"
          firework.style.left = `${Math.random() * 100}%`;
          fireworksContainer.appendChild(firework);
        }
      }

      this.shadow.appendChild(div);
    }
  }
  customElements.define("result-comp", ResultComp);
}
